.gutenberg-flexbox-editor-element {
  padding: 0px;
  background-color: #f8f8f8;
  border: 1px solid #cccccc;
  border-radius: 4px;
  position: relative; }
  .gutenberg-flexbox-editor-element__title {
    width: 100%;
    height: auto;
    background-color: #eeeeee;
    font-size: 0.7em;
    font-weight: bold;
    text-transform: uppercase;
    padding: 4px 6px;
    border-radius: 2px; }
  .gutenberg-flexbox-editor-element .class-tags {
    width: 100%;
    padding: 0px 20px; }
    .gutenberg-flexbox-editor-element .class-tags .class-tag {
      font-size: 0.7em;
      padding: 2px 4px;
      background-color: #dddddd;
      margin: 10px 10px 10px 0px; }
  .gutenberg-flexbox-editor-element .block-editor-inner-blocks {
    padding: 16px; }

.col .block-editor-inner-blocks {
  border: 1px solid #777777;
  background-color: rgba(255, 255, 255, 0.7); }

.container .block-editor-block-list__block,
.row .block-editor-block-list__block,
.col .block-editor-block-list__block {
  margin-top: 0px;
  margin-bottom: 10px; }
